import React from 'react';

const TrainingStopWatch = () => {
  const [trainingTime, setTrainingTime] = React.useState(1);
  React.useEffect(
    () => {
      const id = setInterval(() => setTrainingTime(trainingTime + 1), 1000);
      return () => clearInterval(id);
    },
    [trainingTime]
  );


  return (
      <span>{trainingTime}</span>

  );
};

export default TrainingStopWatch;