import React from 'react';

import PushUpsContext from '../state/PushUpsContextState';
import { TrainingContext } from './RunTraining';
import { postJsonDataUrlAfterAPIpushups } from '../../../functions/postJsonData'

import { SCounter, SCounterSmall } from '../personalPushupPlans/Set';
import imgMinus from '../img/minus.png';
import imgPlus from '../img/plus.png';
import CounterTool from './CounterTool';
import BreakTimer from './BreakTimer';


const StartBreakLogData = () => {
  const [breakTimerFinished, setBreakTimerFinished] = React.useState(false);
  let pushupContext = React.useContext(PushUpsContext);
  const [breakDuration, setBreakDuration] = React.useState(0);

  let trainingContext = React.useContext(TrainingContext);
  let [trainingState, setTrainingState] = trainingContext;
  let set = trainingState.main.set;

  //check if reps or duration. if reps, set saved as false:
  const [setWasSavedToHistory, setSetSavedToHistory] = React.useState(trainingState.main.set.reps ? false : true);
  // let setWasSavedToHistory = trainingState.main.set.reps ? false : true;


  // console.log("set", set.reps);
  // let dataTypeMessage = set.reps ? 'Reps done:' : 'Seconds trained:';
  // console.log(set);
  // const [updatedReps, setUpdatedReps] = React.useState(set.reps!=undefined ? set.reps:0);
  const [updatedReps, setUpdatedReps] = React.useState(set.reps);

  let isLastSet = trainingState.main.setNumberFrom1 >= trainingState.main.training.sets.length ? true : false;


  const saveRepsToHistory = async () => {

    let setToSave = {
      reps: updatedReps,
      repsNotes: trainingState.main.set.repsNotes,
      date: new Date()
    }
    trainingState.main.trExecution.sets.unshift(setToSave);
    // console.log(...trainingState.main.trExecution.sets);

    let updatedUser = await postJsonDataUrlAfterAPIpushups('/updateTrainingExecution', trainingState.main.trExecution);
    // console.log("updatedUser XX: ", updatedUser);

    // console.log("SETTING SAVESTATE TO TRUE1");
    pushupContext.updateUser({ ...updatedUser });
    setSetSavedToHistory(true);
    // setTrainingState({ ...trainingState });
    // console.log("SETTING SAVESTATE TO TRUE2");
  }

  //move to next (final) screen if set saved AND timer finished (or this is last set)
  //((wrapped in effect just to prevent warning for simultaneious updates of different components))


  const moveToTheNextStage = () => {
    //save currently finished break duration (and notes)...
    if (breakDuration > 1) {
      trainingState.main.trExecution.sets[0].breakDuration = breakDuration - 1;
      trainingState.main.trExecution.sets[0].breakNotes = trainingState.main.set.breakNotes;
    }

    //move to the next stage...
    if (isLastSet) {
      // console.log("this is the last set.")
      trainingState.stage = 10; //on the end by adding 1 to stage it will finish
    } else {
      let nextSetNumberFrom1 = trainingState.main.trExecution.sets.length + 1;
      trainingState.main.setNumberFrom1 = nextSetNumberFrom1;
      trainingState.main.set = trainingState.main.training.sets[trainingState.main.setNumberFrom1 - 1];
      trainingState.stage = 1;
    }
    setTrainingState({ ...trainingState });
  }
  React.useEffect(() => {
    if ((breakTimerFinished || isLastSet) && setWasSavedToHistory) {
      // console.log("both finished - time to update page")
      moveToTheNextStage();


    }

  }, [breakTimerFinished, setWasSavedToHistory]);

  return (
    <div>
      {!isLastSet ? <span>Break: <BreakTimer external={{ breakTimerFinished, setBreakTimerFinished }} duration={{ breakDuration, setBreakDuration }} /> </span> : <span>This is the last set.</span>}
      {/* Skip break btn. */}  {setWasSavedToHistory && <button onClick={moveToTheNextStage}>Skip break</button>}

      {/* <p>setWasSavedToHistory: {setWasSavedToHistory ? 'true' : 'false'}</p> */}
      {/* <p>breakTimerFinished: {breakTimerFinished ? 'true' : 'false'}</p> */}

      {/* sort reps  */}
      {set.reps && !setWasSavedToHistory &&
        <div>
          <h1>Data Log...</h1>
          <p>(assuming reps needs to be logged, not time)</p>
          <p>Reps done: </p>

          <CounterTool small={1} big={5} value={updatedReps} setValue={setUpdatedReps} />
          <button onClick={saveRepsToHistory}>{isLastSet ? 'Save set and finish training' : 'Save set'}</button>
        </div>}

      {/* show BREAK info if it exists: */}
      {!isLastSet && setWasSavedToHistory && trainingState.main.set.breakNotes &&
        <div>
          <h2>Break info:</h2>
          <p>{trainingState.main.set.breakNotes}</p>
        </div>}

      {/* show next set info if it exists: */}
      {!isLastSet && setWasSavedToHistory &&
        <div>
          <h2>Next set:</h2>
          <p>{trainingState.main.training.sets[trainingState.main.setNumberFrom1].reps ?
            trainingState.main.training.sets[trainingState.main.setNumberFrom1].reps + ' reps'
            : trainingState.main.training.sets[trainingState.main.setNumberFrom1].duration + ' seconds'}
          </p>
          <p>{trainingState.main.training.sets[trainingState.main.setNumberFrom1].repsNotes && trainingState.main.training.sets[trainingState.main.setNumberFrom1].repsNotes}</p>
        </div>}



    </div>
  );
};

export default StartBreakLogData;