import React from 'react';
import pushupsContext from '../state/PushUpsContextState';
import { withRouter } from 'react-router-dom';
import CurrentTrainingInfo from './CurrentTrainingInfo';
import ShowSetToDo from './ShowSetToDo';
import TrainingSwitcher from './TrainingSwitcher';
import TrainingStopWatch from './TrainingStopWatch';

//Training context
export const TrainingContext = React.createContext({});

const RunTraining = ({ history }) => {
  let context = React.useContext(pushupsContext);
  // const [showTrainingInfo, setShowTrainingInfo] = React.useState(true);


  let plan; //template
  let training; //template from plan
  let trExecution;
  let setNumberFrom1;
  let set; //current set I guess
  if (context.user) {
    trExecution = context.user.pushupPlans.current.trainingHistory[0];
    //get plan template from id
    if (context.user.pushupPlans.current.personal.includes('yes')) {//=personal from current user
      // console.log("PERSONAL");
      plan = context.user.pushupPlans.personal.find(plan => plan._id == context.user.pushupPlans.current.planIDType);
      // console.log("CURRENT PLAN TEMPLATE:", plan);
    } else {
      //get public training plan from public list
    };


    const trainingNumberPosition0IsFirstNumber = context.user.pushupPlans.current.trainingToDoNext;
    const numberOfThisTrainingTemplate = plan.schedule[trainingNumberPosition0IsFirstNumber];
    if (numberOfThisTrainingTemplate == 0) {
      //today is your day off
      console.log("Today is your day off.");
    }
    training = plan.trainings[numberOfThisTrainingTemplate - 1] //training 1 is here on position 0
    // console.log("trPlan: ", trPlan);


    setNumberFrom1 = trExecution.sets.length+1;//0 is the first set
    // setNumberToDoNowFrom1;
    // console.log("currentSetNumberFrom0ToDo", currentSetNumberFrom0ToDo);
    set = training.sets[setNumberFrom1-1]
    // console.log("currentSetToDo", currentSetToDo);
    // console.log("SET NUMBER SHOULD BE UPDATED, setNumberFrom1 is now:", setNumberFrom1, set);

  }
  let trainingPackage = {
    plan,
    training,
    trExecution,
    setNumberFrom1,
    set
  };








  //when user data are loaded, update the state, but do it only once (otherwise infinite loop!)
  const [trainingState, setTrainingState] = React.useState({ stage: -1 }); //0 to show training info

  if (context.user && trainingState.stage == -1) {
    setTrainingState({ stage: 0, main: trainingPackage });
    console.log("stage is -1 - RERENDER state and context");
  }
  // if (context.user && trainingState.stage == 2 && ) {
  //   setTrainingState({ stage: 1, main: trainingPackage });
  //   console.log("stage is 2 - RERENDER state and context");
  // }

  //instead of checking for user each time, just do it here once.
  if (!context.user) return <p>User not logged in</p>
  //if we got up to here, we know that user exists; no need to render conditionally...
  return (
    <TrainingContext.Provider value={[trainingState, setTrainingState]}>
      <p>Training duration: <TrainingStopWatch />s.</p>

      <TrainingSwitcher />
    </TrainingContext.Provider>

  );
};

export default withRouter(RunTraining);