import React from 'react';
import { TrainingContext } from './RunTraining';

const BreakTimer = ({ external, duration }) => {
  let trainingContext = React.useContext(TrainingContext);
  let [trainingState, setTrainingState] = trainingContext;
  // const breakTime = trainingState.main.training.sets[trainingState.main.setNumber]


  const [trainingTime, setTrainingTime] = React.useState(trainingState.main.set.breakAfterInSeconds);
  React.useEffect(
    () => {
      console.log(trainingState.main.setNumberFrom1);
      duration.setBreakDuration(duration.breakDuration + 1);
      if (trainingTime < 1) {
        // console.log("time is UP!");
        if (!external.breakTimerFinished) {
          console.log("firing timer FINISHEd");
          external.setBreakTimerFinished(true)
        };

        // let nextSetNumberFrom1 = trainingState.main.trExecution.sets.length + 1;
        // trainingState.main.setNumberFrom1 = nextSetNumberFrom1;
        // trainingState.main.set = trainingState.main.training.sets[trainingState.main.setNumberFrom1 - 1];
        // trainingState.stage = 1;

        // setTrainingState({ ...trainingState });
      };

      const id = setInterval(() => setTrainingTime(trainingTime - 1), 1000);
      return () => clearInterval(id);
    },
    [trainingTime]
  );


  return (
    <span>{trainingTime}</span>
  );
};

export default BreakTimer;